<template>
  <div class="pro_order">
    <div class="order_nav">我的订单</div>
    <div
      class="order_item"
      v-for="(item, index) in orders"
      :key="'orders' + index"
    >
      <div class="order_item_one" v-if="!item.isdel">
        <div class="item_nav">电影订单号:{{ item.bookId }}</div>
        <div class="item_body">
          <img :src="item.picPath" alt="" />
          <div class="movie_msg">
            <span class="msg_mname">{{ item.mname }}</span>
            <span>{{ item.location }}</span>
            <span>{{ item.vh }}厅{{ "   " + item.bookseat }}号座</span>
            <span>{{ item.day + "-" + item.startTime }}</span>
          </div>
          <div class="price">¥{{ item.price }}</div>
          <div class="isdone">
            <span v-if="!item.isDone">已完成</span
            ><span v-if="item.isDone">未完成</span>
          </div>
          <div class="del">
            <div class="del_btn" @click="del(item.bookId, item.isDone, index)">
              删除
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  mounted() {
    this.getorders();
  },
  data() {
    return {
      orders: [], //所有订单信息包括电影场次信息
      orderitem: {
        bookId: "",
        userId: "",
        bookseat: "",
        mname: "",
        sessionid: "",
        isDone: "",
        location: "",
        day: "",
        startTime: "",
        vh: "",
        picPath: "",
        price: "",
        isdel: "", //表示该信息是否被删除了
      }, //单条订单信息临时存放
    };
  },
  methods: {
    //根据用户id查询book信息
    async getorders() {
      let apiurl = this.$store.getters.GETapihttp + "book/UserId";
      let res = await axios.get(apiurl, {
        method: "GET",
        params: {
          userId: this.$store.getters.GETme.userId,
        },
      });
      for (var item of res.data) {
        //往订单对象放入 订单号，场次号，座位号，用户号，电影名
        // console.log(item);
        this.orderitem.bookId = item.bookId;
        this.orderitem.sessionid = item.sessionId;
        this.orderitem.bookseat = item.bookseat;
        this.orderitem.userId = item.userId;
        this.orderitem.mname = item.mname;
        this.orderitem.isDone = item.isDone;
        this.orderitem.isdel = false;
        await this.getsession(this.orderitem.sessionid);
        await this.getmovie(this.orderitem.mname);
        //console.log(this.orderitem);
        var item = { ...this.orderitem };
        this.orders.unshift(item);
      }

      // console.log(this.orders);
    },

    //根据sessionid查询场次信息
    async getsession(sessionid) {
      let apiurl = this.$store.getters.GETapihttp + "session/ById";
      let res = await axios.get(apiurl, {
        method: "GET",
        params: {
          sessionId: sessionid,
        },
      });
      //   console.log(res.data);
      // this.session = res.data[0];
      this.orderitem.day = res.data[0].day;
      this.orderitem.startTime = res.data[0].startTime;
      this.orderitem.vh = res.data[0].vh;
      this.orderitem.location = res.data[0].location;
    },
    //根据电影名获取电影信息
    async getmovie(mname) {
      let apiurl = this.$store.getters.GETapihttp + "movie/ByName";
      let res = await axios.get(apiurl, {
        method: "GET",
        params: {
          mName: mname,
        },
      });
      // console.log(res.data);
      this.orderitem.picPath = res.data[0].picPath;
      this.orderitem.price = res.data[0].price;
      // console.log(this.orderitem);
    },

    //删除订单信息
    del(bookId, isDone, index) {
      if (isDone != false) {
        let apiurl = this.$store.getters.GETapihttp + "book/delete";
        let res = axios.get(apiurl, {
          method: "GET",
          params: {
            bookId: bookId,
          },
        });
        this.orders[index].isdel = true;
      } else {
        alert("电影订单尚未完成，无法删除");
      }
    },
  },
};
</script>

<style lang="less" scoped>
.pro_order {
  width: 100%;
  position: relative;
}
.order_nav {
  width: 81rem;
  height: 6rem;
  margin-left: 4rem;
  line-height: 6rem;
  font-size: 1.7rem;
  color: rgb(15, 189, 229);
  border-bottom: 1px solid rgba(100, 100, 100, 0.2);
}
.order_item_one {
  border: 1px solid rgba(150, 150, 150, 0.5);
  width: 90%;
  margin-left: 5%;
  margin-top: 2rem;
}
.item_nav {
  height: 5rem;
  line-height: 5rem;
  width: 100%;
  font-size: 1.2rem;
  color: rgba(100, 100, 100);
  background-color: rgb(247, 247, 247);
}
.item_body {
  width: 100%;
  height: 12rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  img {
    position: relative;
    width: 7%;
    margin-left: 3%;
    height: 8rem;
  }
}
.movie_msg {
  width: 50%;
  height: 12rem;
  display: flex;
  flex-direction: column;
  .msg_mname {
    margin-top: 2rem;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }
}
.price {
  width: 10%;
  height: 12rem;
  line-height: 12rem;
}
.isDone {
  width: 10%;
  height: 12rem;
  line-height: 12rem;
}
.del {
  width: 10%;
  height: 12rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .del_btn {
    height: 3rem;
    line-height: 3rem;
    width: 5rem;
    text-align: center;
    cursor: pointer;
    color: #fff;
    background-color: rgb(15, 189, 229);
  }
  .del_btn:hover {
    background-color: rgb(78, 199, 226);
  }
}
</style>