<template>
  <div class="ctcinema">
    <div v-if="iswatch" class="zhezhao"></div>
    <div class="w">
      <div
        class="getin"
        v-for="(item, index) in vhlist"
        :key="'vhlist' + index"
      >
        <img :src="item.picPath" alt="" />
        <div class="vh_msg">
          <span class="vh_mname">{{ item.mname }}</span>
          <span class="vh_time">{{ item.day + "-" + item.startTime }}</span>
          <span class="vh_location">{{ item.location }}</span>
          <span class="vh_vhname">{{ item.vhname }}</span>
          <span class="vh_playstate"
            ><span v-if="item.playstate">正在播放</span>
            <span v-if="!item.playstate">尚未播放</span>
          </span>
          <div class="ct_start" @click="ctstart(item)">放映电影</div>
          <div class="ct_end" @click="ctend(item)">关闭电影</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ct_log from "../tools/ct_log";
import socket from "../socket";
import axios from "axios";
export default {
  beforeRouteEnter: (to, from, next) => {
    next((vm) => {
      ct_log.logcheck(vm, to);
    });
  },

  mounted() {
    //监听别人进入影厅
    socket.on("joinvh", (vhsocket, userName) => {
      console.log("通知通知" + userName + "进入了房间");
    });
    this.getvh();
  },
  data() {
    return {
      vhlist: [],
      vhitem: {
        vhId: "",
        vhname: "",
        vhsocket: "",
        sessionid: "",
        mname: "",
        location: "",
        day: "",
        startTime: "",
        picPath: "",
        playPath: "",
        playstate: false, //当前电影播放状态
      }, //单条订单信息临时存放
      iswatch: false, //表示没有观看电影
    };
  },
  methods: {
    //获取该用户可以进入的影厅列表与对应电影和场次
    async getvh() {
      let apiurl = this.$store.getters.GETapihttp2 + "vh/ct_vhlist";
      let res = await axios.get(apiurl, {
        method: "GET",
        params: {
          userId: this.$store.getters.GETme.userId,
        },
      });

      for (var item of res.data) {
        //往订单对象放入 订单号，场次号，座位号，用户号，电影名
        // console.log(item);
        this.vhitem.vhId = item.vhid;
        this.vhitem.vhname = item.vhname;
        this.vhitem.vhsocket = item.vhsocket;
        this.vhitem.sessionid = item.sessionId;
        await this.getsession(this.vhitem.sessionid);
        await this.getmovie(this.vhitem.mname);
        // console.log(this.vhitem);
        var item = { ...this.vhitem };
        this.vhlist.push(item);
      }
    },

    //根据sessionid查询场次信息
    async getsession(sessionid) {
      let apiurl = this.$store.getters.GETapihttp + "session/ById";
      let res = await axios.get(apiurl, {
        method: "GET",
        params: {
          sessionId: sessionid,
        },
      });
      this.vhitem.mname = res.data[0].mname;
      this.vhitem.day = res.data[0].day;
      this.vhitem.startTime = res.data[0].startTime;
      this.vhitem.location = res.data[0].location;
    },
    //根据电影名获取电影信息
    async getmovie(mname) {
      let apiurl = this.$store.getters.GETapihttp + "movie/ByName";
      let res = await axios.get(apiurl, {
        method: "GET",
        params: {
          mName: mname,
        },
      });
      this.vhitem.picPath = res.data[0].picPath;
      this.vhitem.playPath = res.data[0].playPath;
    },
    //管理员控制电影打开
    ctstart(item) {
      item.playstate = true;
      socket.emit("ctstart", item.vhsocket);
    },
    //管理员控制电影关闭
    ctend(item) {
      item.playstate = false;
      socket.emit("ctend", item.vhsocket);
    },
  },
};
</script>

<style lang="less" scoped>
.ctcinema {
  width: 100%;
  height: 60rem;
  background-size: 100% 100%;
}
.w {
  height: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 2rem;
}
.myvideo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.getin {
  width: 40%;
  height: 30rem;
  background-color: rgba(151, 155, 158, 0.2);
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
  img {
    width: 50%;
    height: 100%;
  }
}
.vh_msg {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .vh_mname {
    height: 3rem;
    line-height: 3rem;
    font-size: 2rem;
    color: rgb(239, 66, 56);
  }
  .vh_time {
    font-size: 1.5rem;
    color: rgb(0, 0, 0);
  }
  .vh_playstate {
    color: rgb(239, 66, 56);
  }
  .ct_start,
  .ct_end {
    margin-top: 3rem;
    height: 4rem;
    width: 70%;
    text-align: center;
    margin-left: 15%;
    line-height: 4rem;
    cursor: pointer;
    font-size: 1.5rem;
    color: #fff;
    background-color: rgb(15, 189, 229);
  }
  .ct_start:hover,
  .ct_end:hover {
    background-color: rgb(15, 189, 229);
  }
}
</style>